import { useEffect, useState } from "react"
const Cards = ({ movie }) => {
    useEffect(()=> {
        setNum(movie.vote_average);
        Color();
        Ring();
    })
    const [num, setNum] = useState(5)
    const [color, setColor] = useState('')
    const [ring, setRing] = useState('ring-white')
    const Color = () => {
        if(num <= 5){
            setColor('text-red-500')
        }else{
            setColor('text-slate-200')
        }
    }
    const Ring = () => {
        if(num <= 5){
            setRing('ring-red-500')
        }else if(num <= 7){
            setRing('ring-yellow-300')
        }else{
            setRing('ring-green-400')
        }
    }
    return(
        <button className='min-h-full bg-gradient-to-br from-yellow-600 to-stone-400 rounded-lg'> 
            <div className='Cards'>
                <img className='Movie-image' src={`${process.env.REACT_APP_BASEIMGURL}/${movie?.poster_path}`} alt={`${movie.title} image`}></img>
                <div className='Movie-title drop-shadow-md'>{movie.title}</div>
                <div id='ring' className={`ring-2 ${ring} ring-inset absolute top-4 right-4 bg-gray-700 rounded-full w-8 h-8 sm:w-12 sm:h-12`}>
                    <div className={`relative top-[5px] text-sm sm:top-[12px] ${color} sm:text-[22px] sm:w-[28px] sm:ml-[9px] overflow-hidden`}>{movie.vote_average}</div>
                </div>               
            </div>
        </button>    
    )
}

export default Cards